.loader {
    border: 16px solid lightblue;
    border-top: 16px solid palevioletred;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
    display: flex;
    justify-content: center;
}

.hidden {
    visibility: hidden;
}

@keyframes spin {
    0% { transform: rotate(0deg);}
    100% {transform: rotate(360deg);}   
}